import { Component, OnInit, HostListener, OnDestroy } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { CheckoutService } from 'src/app/core/services/checkout.service';
import { Router, ActivatedRoute } from '@angular/router';
import { ValidatorService } from 'src/app/core/services/validator.service';
import { MainSharedService } from 'src/app/util/shared.service';
import { FormatNumber } from 'src/app/util/formatNumber.util';
import { GtmService } from 'src/app/core/services/gtm.service';
import { UserService } from 'src/app/core/services/user.service';
import { Title } from '@angular/platform-browser';
import { OrderService } from 'src/app/core/services/order.service';
import { IStatusTimeline } from 'src/app/shared/steps-timeline/steps-timeline.component';
import { AlertMessageComponent } from 'src/app/shared/alert-message/alert-message.component';
import { SubSink } from 'subsink';
import { CheckLoginService } from 'src/app/core/services/checklogin.service';
import { environment } from 'src/environments/environment';
import { EventBusService, Events } from 'src/app/core/services/eventbus.service';
import { CartaoCreditoNovoModel } from 'src/app/shared/models/cartao-credito-novo.model';
import { QuotationService } from 'src/app/core/services/quotation.service';
import { SeoService } from 'src/app/core/services/header/seo.service';
export type IStatus = 'resume' | 'method_payment' | 'own_card' | 'resp_fin';
import { MediaService } from 'src/app/core/services/media.service';
import { CPFValidatorService } from 'src/app/core/services/cpf-validator.service';
import { LocalStorage } from 'src/app/util/local-storage.service';

@Component({
  selector: 'app-checkout',
  templateUrl: './checkout.component.html',
  styleUrls: ['./checkout.component.scss'],
  providers: [AlertMessageComponent]
})
export class CheckoutComponent implements OnInit, OnDestroy {

  @HostListener('window:resize', ['$event'])
  onResize(event) {
    if (window.innerWidth < 768) {
      this.hideButtonMobile = false;
      this.isMobile = true
    } else {
      this.hideButtonMobile = true;
      this.isMobile = false
    }
  }

  // Types cards
  public ownCard: boolean = true
  public newCard: boolean = false
  public holderCard: boolean = false
  public viewCards: boolean = true
  public dataCards: boolean = false;
  private cardData: any;
  private dataNewCard = new CartaoCreditoNovoModel;

  // Validate cards
  public cardValid: boolean = false;
  public newCardValid: boolean = false;

  // Timer expired
  private timerExpired: number = 900000;
  private timerClosed: boolean = false;

  public urlImg: string = environment.GCP_IMAGES;

  // Utils
  private formatNumber: FormatNumber = new FormatNumber();

  // Page Configuration
  public statusStep: IStatusTimeline = 'pagamento';
  public status: IStatus;

  // Page Control
  public isMobile: boolean;
  public onDemand: boolean;
  public isOffer: boolean = false;
  public showMsg: boolean = true;
  public showHelp: boolean = false;
  public showHelpDia: boolean = false;
  public termsAccepted: boolean = false;
  public hideButtonMobile: boolean = false;
  private triggerAlert: boolean = true;

  // Quotation values
  public excessKm: string;
  public finalDays: Number = undefined;

  // Dados do usuário
  public user: any;
  public userType: any;
  public userAddress: string;

  // Dados do pedido
  public orderId: any = null;
  public reservaCodigo: number = null;
  public month: number = null;
  public month_promo: number = null;
  public pedido: any;
  public reserva: any;
  public vehicleFeatures: Array<string>;
  public srcVehicleImg: string;
  public promoValue: string;
  public totalValue: string;
  public totalValueMonth: string;
  public totalResp: string;
  public reservaLojaNome: string;
  public reservaEndereco: string;
  public reservaCidade: string;
  public reservaEstado: string;
  public pagadorID: any;
  public isOfferBlackFriday: boolean = false;

  public isLoadingCards: boolean = true;
  public isLoadingPrice: boolean = true;

  //Dados da Pessoa
  public celular: any = null;
  public telefone: any = null;

  // Features variables
  public carName: any;
  public carDescription: any;
  public franquiaKm: any;
  public periodo: any;
  public FinalDePlaca: any;
  public protecao: any;

  // Variable payer
  public isPayer: boolean = false;

  // Warning variables
  public enableWarning: boolean = true;
  public warningMessage: string = 'O pagamento do primeiro aluguel e produtos adicionais eventualmente contratados ocorrerá após a inserção dos dados do cartão.';

  public isPromo: boolean = false;
  public messagePromo: string;
  public messageSemPromo: string;
  public dataBitrix: any = null;
  public fonteBitrix: any = null;

  get continueCheckout(): boolean {
    if (this.ownCard || this.newCard) {
      return (this.termsAccepted && this.newCardValid && !this.cardValid) || (this.termsAccepted && this.cardValid && !this.newCardValid)
    } else if (this.holderCard) {
      return (this.termsAccepted && this.formHolder.valid && this.holderCard)
    }
    return false
  }

  get continueStep(): boolean{
    if(this.holderCard ){
      return (this.holderCard && this.formHolder.controls.cpfHolder.valid) ? true : false;
    }
    return true;
  }

  get prazoEntrega() {
    return this.pedido && this.pedido.frota ? this.pedido.frota.PrazoEntrega : '---'
  }

  // Choosen Car Options
  public carOpt: any;

  public protectionList = new Array<any>();
  public extraProtectionList = new Array<any>();
  public additionalServiceList = new Array<any>();
  public dias = [
    {value: 5, viewValue: '05'},
    {value: 10, viewValue: '10'},
    {value: 15, viewValue: '15'},
    {value: 20, viewValue: '20'},
    {value: 25, viewValue: '25'},
  ];

  private defaultUtm = {
    utm_source: '',
    utm_medium: '',
    utm_campaign: '',
    utm_content: '',
    utm_term: '',
  }

  private sub = new SubSink();

  formHolder = new FormGroup({
    cpfHolder: new FormControl('', [Validators.required, this.cpfValidatorService.isValidCpf, Validators.minLength(11)]),
    nameHolder: new FormControl('', [Validators.required]),
    emailHolder: new FormControl('', [Validators.required, Validators.email])
  })

  formCoupon = new FormGroup({
    coupon: new FormControl(''),
  })

  formHolderPayDay = new FormGroup({
    diaCobranca: new FormControl('', [Validators.required])
  })


  constructor(
    private titleS: Title,
    private router: Router,
    private route: ActivatedRoute,
    private quotationS: QuotationService,
    private gtm: GtmService,
    private shared: MainSharedService,
    private validator: ValidatorService,
    private cpfValidatorService: CPFValidatorService,
    private userS: UserService,
    private checkoutS: CheckoutService,
    private orderService: OrderService,
    private alertMsg: AlertMessageComponent,
    private checkLoginService: CheckLoginService,
    private eventBusService: EventBusService,
    private seoService: SeoService,
    private mediaService: MediaService,
    private localStorage: LocalStorage,
  ) { }

  ngOnInit() {

    this.setPageStatus('resume');
    this.checkLoginService.check();

    this.titleS.setTitle('Zero Km - Checkout');
    this.seoService.createLinkForCanonicalURL();
    this.gtm.addPageGTM('Pageview ZeroKM', this.router.url, 'Zero Km - Checkout');
    this.shared.setHideOnCheckout(true);
    this.shared.removeContinueClicked();
    localStorage.removeItem('continueClicked');

    this.sub.sink = this.eventBusService.on(Events.sendDataCard, card => this.receiveDataCard(card));
    this.sub.sink = this.eventBusService.on(Events.sendNewCard, card => this.receiveDataCard(card));
    this.sub.sink = this.eventBusService.on(Events.checkCards, cards => this.checkCards(cards));
    this.sub.sink = this.eventBusService.on(Events.changeCardType, type => this.changeCardType(type));

    this.onResize(null);

    this.getUserData();
    this.checkUserType();

    this.orderId = this.route.snapshot.params.orderId;
    this.reservaCodigo = this.route.snapshot.params.reservaCodigo;

    // Verify payer
    const urlPath = this.router.url.split('?')[0];
    const urlResponsavel = urlPath.includes('/assinatura/responsavel/pagamento');

    if (urlResponsavel) {
      this.checkoutS.verifyPayer(this.orderId).subscribe(verify => {
        if (verify.data.permission === 1) {
          this.isPayer = true
        } else {
          this.isPayer = false
          this.alertMsg.openDialog({
            tipo: 'message',
            titulo: 'Ops!',
            mensagem: 'Este pedido pertence à um outro Responsável Financeiro.'
          }, 'message', true).then(dialogRef => {
            this.sub.sink = dialogRef.afterClosed().subscribe(() => {
              this.router.navigate([`/`]);
            });
          });
          return
        }
      })
    }

    this.getOrder(this.orderId);

    setTimeout(() => {
      if (!this.timerClosed && !this.shared.vehicleDetails.sellerCode) {
        this.getMessageExpired();
      }
    }, this.timerExpired)
  }

  private checkCards(cards) {
    if (cards.length > 0) {
      this.dataCards = true;
      this.newCard = false;
    } else {
      this.dataCards = false;
      this.newCard = true;
    }
    this.isLoadingCards = false;
  }

  public receiveDataCard(card) {
    if (card && card.CartaoID && !this.holderCard && !this.newCard) {
      this.cardData = card;
      this.cardValid = true
      this.newCardValid = false
    } else {
      this.cardValid = false
      this.newCardValidate(card);
      this.dataNewCard = card
    }
  }

  setPageStatus(status: IStatus) {
    window.scrollTo(0, 0);
    this.status = status;
  }

  getDiasCobranca(){
    this.dias;
  }

  hideWarning() {
    this.showMsg = false;
  }

  showCvvHelp() {
    this.showHelp = true;
  }

  hideCvvHelp() {
    this.showHelp = false;
  }

  showDiaHelp() {
    this.showHelpDia = true;
  }

  hideDiaHelp() {
    this.showHelpDia = false;
  }


  checkNumbers(event) {
    this.validator.checkNumbers(event);
  }

  checkIsOffer(promoValue: Number) {
    if (!this.onDemand) {
      this.isOffer = promoValue && promoValue > 0 ? true : false;
    }
  }

  checkUserType() {
    if (this.shared.user) {
      this.user = this.shared.user;
      this.userType = this.shared.user.colaborador && this.shared.choosenView !== 'seller' ? 'FC' : 'PF';
    } else {
      this.userType = 'PF';
    }
  }

  checkPickupStore() {
    if (this.pedido && this.pedido.delivery_point && Object.keys(this.pedido.delivery_point).length > 0) {
      return this.pedido.delivery_point.FilialID === '380' ? false : true;
    } else {
      return false;
    }
  }

  checkOrderStatus(statusId) {
    let obj;
    switch (statusId) {
      case 0:
        obj = {
          url: `/assinatura/pagamento${this.orderId}`,
          titulo: 'Você já possui um pedido similar.',
          mensagem: `Atualizamos o pedido ${this.orderId} que já possui um cartão cadastrado.Você será redirecionado para continuar o processo de compra do veículo.`
        }
        break;
      case 1: // 1. Adicionado - Pedido no carrinho - redirecionar para checkout
      case 5: // 5. Aguardando Pagamento - Processando pagamento - redirecionar para checkout
        return;
      case 3: // 3. Enviado - Processando registro no sistema interno
        obj = {
          url: `/assinatura/capturar-foto/${this.orderId}`,
          titulo: 'Você já possui um pedido similar.',
          mensagem: `Atualizamos o pedido ${this.orderId} que já possui um cartão cadastrado.Você será redirecionado para continuar o processo de compra do veículo.`
        }
        break;
      // case 1 : //1. Aguardando documentação - cliente vai passar a cnh e selfie na loja
      case 4: // 4. Pedido Registrado - Processamento registro no sistema interno
      case 6: // 6. Em processamento - Processando o pedido
        obj = {
          url: `/assinatura/sucesso/${this.orderId}`,
          titulo: 'Seu pedido está em andamento.',
          mensagem: `Você será redirecionado para ver os detalhes do pedido ${this.orderId}.`
        }
        break;
      case 2: // 2. Abandonado - Pedido não concluido
      case 7: // 7. Cancelado - Pedido cancelado após o processamento
      case 8: // 8. Reprovado - Pedido com o requisitos não contemplados
      case 9: // 9. Aprovado - Pedido aprovado | Pronta Entrega -> "Disponível para retirada" & Sob Demanda -> "Preparação e transporte"
      default:
        obj = {
          url: `/assinatura/sucesso/${this.orderId}`,
          titulo: 'Seu pedido já foi finalizado.',
          mensagem: `Você será redirecionado para ver os detalhes do pedido ${this.orderId}.`
        }
        break;
    }
    this.alertMsg.openDialog({
      ...obj,
      tipo: 'message',
      icon: 'car-icon.svg'
    }, 'message', true).then(dialogRef => {
      dialogRef.afterClosed().subscribe(() => {
        this.router.navigate([obj.url]);
      });
    });
  }

  checkTerms() {
    this.alertMsg.openDialog({
      isMobile: this.isMobile,
      titulo: 'Atenção!',
      tipo: 'message',
    }, 'terms', true).then(dialogRef => {
      this.sub.sink = dialogRef.componentInstance.yes.subscribe(() => {
        this.termsAccepted = true
      });
      this.sub.sink = dialogRef.componentInstance.not.subscribe(() => {
        this.termsAccepted = false
      });
    });
  }

  changeCheck(check) {
    check ? this.checkTerms() : this.termsAccepted = false
  }

  get vehicle() {
    return this.pedido && this.pedido.veiculo ? this.pedido.veiculo : null;
  }

  get color() {
    const hex1 = this.pedido && this.pedido.cor && this.pedido.cor.Hex1 ? this.pedido.cor.Hex1 : 'white';
    return {
      nome: this.pedido && this.pedido.cor && this.pedido.cor.Cor ? this.pedido.cor.Cor : '',
      hex1: hex1,
      hex2: this.pedido && this.pedido.cor && this.pedido.cor.Hex2 ? this.pedido.cor.Hex2 : hex1
    };
  }

  getUserData() {
    if (this.shared.user) {
      this.user = this.shared.user;
      this.userAddress = this.userS.getUserAddress(this.shared.user);
    }
  }

  getCoupon() {
    //const formCoupon = this.formCoupon.getRawValue();
    //let coupon = formCoupon.coupon
  }

  getHolderCPF() {
    const formData = this.formHolder.getRawValue();
    const cpf = formData.cpfHolder;

    if ((cpf.length === 11 || cpf.length === 14)  && cpf === this.shared.user.cpf) {
        this.formHolder.controls['cpfHolder'].setValue('');
        this.alertMsg.openDialog({
          tipo: 'message',
          titulo: 'Ops!',
          mensagem: 'O CPF do Responsável Financeiro não pode ser igual ao CPF do condutor.'
        }, 'message', true);
    }
  }

  createOrderOnDemand() {
    this.shared.onDemand = {
      bitrix: this.dataBitrix,
      fonte: this.fonteBitrix,
      nome: this.shared.user.razaosocial,
      email: this.shared.user.email,
      cpf: this.shared.user.cpf,
      telefone: this.telefone
    };

    let utmData = this.defaultUtm;
    if (this.localStorage.get('utm')) {
      utmData = JSON.parse(this.localStorage.get('utm'));
    }

    this.orderService.createOnDemand(
      this.shared.onDemand,
      this.shared.vehicleDetails,
      this.shared.tokenVendedor,
      utmData,
    ).subscribe(res => {}, ex => {});
  }

  getOrder(orderId: Number) {
    this.sub.sink = this.orderService.getOrder(orderId).subscribe(data => {
      if (!data || !data.pedido || !data.pedido.PedidoFrotaID) {
        this.shared.setCounterLoading(false);
        this.alertMsg.openDialog({
          tipo: 'message',
          titulo: 'Algo deu errado!',
          mensagem: 'Não foi possível carregar as informações do seu pedido. Tente novamente mais tarde.'
        }, 'message', true).then(dialogRef => {
          this.sub.sink = dialogRef.afterClosed().subscribe(() => {
            if (data && (!data.pedido || !data.pedido.PedidoFrotaID)) {
              this.router.navigate(['/minha-conta']);
            }
          });
        });

        if (!data) {
          this.router.navigate(['/minha-conta']);
        }
        return void (0);
      }
      this.celular = data && data.pedido && data.pedido.celular ? data.pedido.celular : null;
      this.telefone = data && data.pedido && data.pedido.telefone ? data.pedido.celular : null;

      if (data && data.pedido && data.pedido.ReservaID) {
        this.shared.setCounterLoading(false);
        this.alertMsg.openDialog({
          tipo: 'message',
          titulo: 'Algo deu errado!',
          mensagem: 'Este pedido já possui uma reserva, por favor, clique para visualiza-la.'
        }, 'message', true).then(dialogRef => {
          this.sub.sink = dialogRef.afterClosed().subscribe(() => {
            this.isPayer ? this.router.navigate([`/assinatura/responsavel/sucesso/${orderId}`]) : this.router.navigate([`/assinatura/pedido/${orderId}`]);
          });
        });
        return
      }

      if (data && data.pedido.PedidoFrotaStatusID !== 11) {
        this.pedido = data.pedido;
        this.carName = this.pedido.frota.Marca + " " + this.pedido.frota.Modelo;
        this.carDescription = this.pedido.frota.Versao;
        this.franquiaKm = this.pedido.tarifa.Franquia;
        this.periodo = this.pedido.tarifa.Periodo;
        this.FinalDePlaca = this.pedido.FinalDePlaca;
        this.protecao = this.pedido.protecao.Nome;
        this.reservaLojaNome = this.pedido.loja.Nome
        this.reservaEndereco = this.pedido.loja.Endereco.split('/')[0].trim()
        this.reservaCidade = this.pedido.loja.Endereco.split('/')[1].split('-')[0].trim()
        this.reservaEstado = this.pedido.loja.Endereco.split('/')[1].split('-')[1].trim()
        this.onDemand = data.pedido.pedidoFrotaStatusID === 3 ? true : false;

        // Set Vehicle Images
        this.mediaService.getMedia('fipes-image').subscribe(res => {

          const fipeImage = res.data.filter(
            res => (res.titulo === data.pedido.frota.Fipe)
          );

          let img_veiculo_nao_encontrado : string = "assets/img/vehicles/carro-nao-encontrado.png";

          if (fipeImage.length > 0 && fipeImage[0].src !== undefined && fipeImage[0].type !== undefined) {
            if (this.router.url.includes('pagamento') || this.router.url.includes('sucesso')) {
              this.srcVehicleImg = fipeImage[0].src_pagamento.length > 0 ? fipeImage[0].src_pagamento : img_veiculo_nao_encontrado;
            } else {
              this.srcVehicleImg = fipeImage[0].src.length > 0 ? fipeImage[0].src : img_veiculo_nao_encontrado;
            }
          }else{
            this.srcVehicleImg = img_veiculo_nao_encontrado;
          }
         });
        //this.srcVehicleImg = data.pedido.frota.Fipe ? `${this.urlImg}/vehicles-fipe/${data.pedido.frota.Fipe}.png` : 'false';
        this.getTariff();
        this.shared.setCounterLoading(false);
      } else {
        this.shared.setCounterLoading(false);
        this.timerClosed = true;
        this.getMessageExpired();
      }
    }, ex => {
      this.shared.setCounterLoading(false);
      this.alertMsg.openDialog({
        tipo: 'message',
        titulo: 'Algo deu errado!',
        mensagem: 'Não foi possível carregar as informações do seu pedido. Tente novamente mais tarde.'
      }, 'message', true);
      this.router.navigate(['/minha-conta'], { replaceUrl: true, queryParamsHandling: 'merge' });
    });
  }

  errorHandlerImg(event) {
    event.target.src = "assets/img/vehicles/carro-nao-encontrado.png";
  }

  getMessageExpired() {
    const msg = 'O tempo para concluir o seu pedido expirou! Caso ainda tenha interesse, faça um novo pedido.';
    this.alertMsg.openDialog({
      icon: 'expired.svg',
      tipo: 'message',
      titulo: 'Ops! Seu pedido expirou.',
      mensagem: msg
    }, 'message', true).then(dialogRef => {
      dialogRef.afterClosed().subscribe(() => {
        this.router.navigate(['/']);
      });
    });
  }

  getCoinsurance(groupId, protectionCode) {
    const coinsuranceCode = new Array();
    coinsuranceCode['MB0KM'] = 'CPBZKM';
    coinsuranceCode['MC0KM'] = 'CPCZKM';
    const coinsurances = JSON.parse(localStorage.getItem('coinsurances'));
    const protectionCoinsurance = coinsurances ? coinsurances.filter(opt => opt.sigla === coinsuranceCode[protectionCode])[0] : null;
    const coinsuranceValue = protectionCoinsurance ? protectionCoinsurance.valores.filter(opt => opt.grupo_id === groupId)[0] : null;
    return coinsuranceValue ? this.formatNumber.getBRLCurrency(Number(coinsuranceValue.valor)) : null;
  }

  getProtectionString(groupId, protection) {
    const copart = this.getCoinsurance(groupId, protection.initials);
    return copart ? `${protection.name} (Valor da coparticipação: ${copart})` : protection.name;
  }

  setOrderGTM() {
    const vehicle = this.vehicle;
    const vehicleDetails = {
      id: vehicle.id,
      id_vetor: vehicle.id,
      sku: vehicle.sku,
      fipe: vehicle.version.fipe,
      brand: vehicle.version.model.brand.name,
      model: vehicle.version.model.name,
      version: vehicle.version.version,
      images: [vehicle.img],
      color: vehicle.color.name,
      plate: vehicle.plate,
      serialItems: {
        absBrake: vehicle.accessories.absBrake,
        airBag: vehicle.accessories.airBag,
        airConditioning: vehicle.accessories.airConditioning,
        doors: vehicle.doors,
        fuel: vehicle.fuel.name,
        gear: vehicle.gear
      },
      period: this.pedido.period.months,
      mileage: this.pedido.franchise,
      tariff: this.pedido.tariff_initials,
      pickupStore: this.pedido.delivery_point,
      protection: this.pedido.protections,
      extraProtections: this.pedido.extra_protections,
      additionalServices: this.pedido.additional_services,
      values: {
        tariff: '0',
        protections: '0',
        extraProtections: '0',
        additionalServices: '0',
        promotional: this.pedido.promotional_price,
        total: this.pedido.total_price
      }
    }
    this.gtm.addOrderGTM('checkout', this.onDemand, this.orderId, vehicleDetails);
  }

  submitRF() {
    this.shared.setCounterLoading(true);

    const formData = this.formHolder.getRawValue();

    const paymentRF = {
      cpf_rf: formData.cpfHolder,
      nome_rf: formData.nameHolder,
      email_rf: formData.emailHolder,
      pedido_id: this.orderId
    }

    this.shared.emailPayer = formData.emailHolder

     this.userS.sendRespFinanceiro(paymentRF).subscribe(res => {
       if (res !== undefined && res != null && res && res.success) {
         this.shared.RF_PessoaID = res.data.PessoaID ? res.data.PessoaID : null
         this.submit();
         this.shared.setCounterLoading(false);
       }
     }, ex => {
       this.shared.setCounterLoading(false);
       const msg = 'Não foi possível enviar os dados do pagador. Tente novamente mais tarde.';
       this.alertMsg.openDialog({
         tipo: 'message',
         titulo: 'Algo deu errado!',
         mensagem: msg
       }, 'message', true).then(dialogRef => {
         dialogRef.afterClosed().subscribe(() => {
           this.router.navigate(['/']);
         });
       });
     })

  }

  submit() {
    const formDataPay = this.formHolderPayDay.getRawValue();

    this.shared.setCounterLoading(true);

    let reserva = {}
    const isUserPayer = this.isPayer ? parseInt(this.user.PessoaID) : this.shared.RF_PessoaID

    if (isUserPayer) {
      reserva = {
        pagador_id: this.isPayer ? parseInt(this.user.PessoaID) : parseInt(this.shared.RF_PessoaID),
        pedido_id: parseInt(this.orderId),
        dia_cobranca: formDataPay.diaCobranca
      }
      this.shared.OrderType = 'RF';
    } else {
      reserva = {
        pedido_id: parseInt(this.orderId),
        dia_cobranca: formDataPay.diaCobranca
      }
      this.shared.OrderType = 'C';
    }

    if (this.holderCard) {
      // if (this.holderCard && this.shared.RF_PessoaID === null) {
      this.shared.removeRF_PessoaID();
      this.shared.removeRedirectToRF();
      this.shared.setCounterLoading(false);
      this.router.navigate([`/assinatura/sucesso/${this.orderId}`]);
    } else {
      this.sub.sink = this.checkoutS.saveReserve(reserva).subscribe(res => {

        if (res !== undefined && res != null && res && res.success && res.data.reservaCodigo && !this.holderCard && !this.shared.RF_PessoaID) {

          let payment = {}

          if (!this.holderCard && this.ownCard && this.cardValid && !this.newCardValid) {
            payment = {
              cpf: this.user.cpf,
              carteira_id: 1,
              reserva_id: res.data.reservaCodigo,
              CartaoID: this.cardData.CartaoID,
              pedido_frota_id: this.orderId,
              order_hash: this.shared.totalEncrypted
            }
          } else if (!this.holderCard && this.newCard && !this.cardValid && this.newCardValid) {
            payment = {
              cpf: this.dataNewCard.cpf,
              card_holder: this.dataNewCard.Titular,
              numero_cartao: this.dataNewCard.Nro.replace(/\D/g, ''),
              validade: this.dataNewCard.Validade,
              cvv: this.dataNewCard.cvv,
              bandeira_id: this.dataNewCard.BandeiraID,
              carteira_id: this.dataNewCard.CarteiraID,
              reserva_id: res.data.reservaCodigo,
              tipo: 'C',
              ip: this.dataNewCard.Ip,
              pedido_frota_id: this.orderId
            }
          }

          this.checkoutS.savePaymentData(this.user.pessoa_id, payment).subscribe(res => {
            if (res !== undefined && res != null && res && res.success) {
              this.shared.removeRF_PessoaID();
              this.shared.removeRedirectToRF();
              this.shared.setCounterLoading(false);
              this.shared.removeTotalEncrypted();
              // this.setOrderGTM();
              this.router.navigate([`/assinatura/sucesso/${this.orderId}`]);
            }
          }, ex => {
            this.shared.setCounterLoading(false);

            if (this.cardValid && !this.newCardValid) {

              const msg = 'Infelizmente o seu pedido não pôde ser processado. Entre em contato com a operadora do seu cartão e reinicie o processo de contratação no nosso site.';
              this.alertMsg.openDialog({
                tipo: 'message',
                titulo: 'Ops! Algo está errado.',
                mensagem: msg
              }, 'message', true).then(dialogRef => {
                dialogRef.afterClosed().subscribe(() => {
                  this.router.navigate(['/']);
                });
              });

            } else {
              const msg = 'Não foi possível enviar os dados de pagamento. Tente novamente mais tarde.';
              this.alertMsg.openDialog({
                tipo: 'message',
                titulo: 'Algo deu errado!',
                mensagem: msg
              }, 'message', true).then(dialogRef => {
                dialogRef.afterClosed().subscribe(() => {
                  this.router.navigate(['/']);
                });
              });
            }
          });
        } else {
          this.shared.removeRF_PessoaID();
          this.shared.removeRedirectToRF();
          this.shared.setCounterLoading(false);
          this.router.navigate([`/assinatura/responsavel/sucesso/${this.orderId}`]);
        }
      }, ex => {
        this.shared.setCounterLoading(false);
        const msgError = ex.error.message ? ex.error.message : ex.error.data.msg;
        const msg = 'Não foi possível processar o seu pedido. ' + msgError;
        this.alertMsg.openDialog({
          tipo: 'message',
          titulo: 'Algo deu errado!',
          mensagem: msg
        }, 'message', true).then(dialogRef => {
          dialogRef.afterClosed().subscribe(() => {
            this.router.navigate(['/']);
          });
        });
      });
    }

  }

  newCardValidate(card) {
    card.Bandeira !== '' &&
    card.BandeiraID !== '' &&
    card.CarteiraID !== '' &&
    card.Nro !== '' &&
    card.Titular !== '' &&
    (card.Validade !== '' && card.Validade.toString().length === 10) &&
    card.cpf !== '' &&
    (card.cvv !== '' && card.cvv.toString().length >= 3) &&
    this.newCard &&
    !this.holderCard ? (this.newCardValid = true, this.cardValid = false) : (this.newCardValid = false, this.cardValid = false)
  }

  handleSuccess(invalidCard: boolean = false) {
    if (this.holderCard && this.formHolder.valid) {
      localStorage.setItem('loaderCounter', '0');
      this.shared.setCounterLoading(false);
      this.submitRF();
      return;
    }
    if ((!this.holderCard && this.ownCard && this.cardValid && this.termsAccepted) || (!this.holderCard && this.newCard && this.newCardValid && this.termsAccepted)) {
      if (this.cardValid || this.newCardValid) {
        localStorage.setItem('loaderCounter', '0');
        this.shared.setCounterLoading(false);
        this.submit();
      }
    } else if (invalidCard) {
      localStorage.setItem('loaderCounter', '0');
      this.shared.setCounterLoading(false);
      this.alertMsg.openDialog({
        tipo: 'message',
        titulo: 'Cartão inválido!',
        mensagem: 'Verifique os dados do seu cartão e tente novamente.'
      }, 'message', true);
    } else {
      const msg = 'Verifique se preencheu todos os dados de pagamento e/ou aceitou os termos e condições.';
      this.alertMsg.openDialog({
        tipo: 'message',
        titulo: 'Algo deu errado!',
        mensagem: msg
      }, 'message', true);
    }
  }

  async changeCardType(type) {
    switch (type) {
      case 'ownCard':
        if (!this.ownCard) {
          this.shared.setLoadingAtivo(true);
          this.carOpt.bookingPayload.equipamentos.find((eqp, k) => {
            if (eqp.tipo === 'RESP_FINAN') {
              this.carOpt.bookingPayload.equipamentos.splice(k);
            }
          });
          await this.quoteReservation(this.carOpt.bookingPayload);
        }
        if (this.cardData && this.cardData.CartaoID) {
          this.cardValid = true
          this.newCardValid = false
        }
        this.ownCard = true;
        this.holderCard = false;
        this.dataCards ? this.newCard = false : this.newCard = true;
        this.warningMessage = 'O pagamento do primeiro aluguel e produtos adicionais eventualmente contratados ocorrerá após a inserção dos dados do cartão.';
        this.shared.removeRF_PessoaID()
        break;
      case 'newCard':
        if (this.cardData && this.cardData.CartaoID) this.cardValid = false
        this.newCardValidate(this.dataNewCard);
        if (!this.newCard) {
          this.shared.setLoadingAtivo(true);
          await this.quoteReservation(this.carOpt.bookingPayload)
        }
        this.newCard = true;
        this.ownCard = false;
        this.holderCard = false;
        this.shared.removeRF_PessoaID()
        break;
      case 'holderCard':
        if (!this.holderCard) {
          this.shared.setLoadingAtivo(true);
          const isHolderCard = {
            tipo: 'RESP_FINAN', // Não trocar esse string | A mudança deve ser feita no BE-0KM
            quantidade: 1
          }
          this.pushIfNotExists(this.carOpt.bookingPayload.equipamentos, isHolderCard);
          await this.quoteReservation(this.carOpt.bookingPayload);
        }
        this.cardValid = false
        this.newCardValid = false;
        this.holderCard = true;
        this.ownCard = false;
        this.newCard = false;
        this.warningMessage = 'O seu pedido será concluído após o responsável financeiro se cadastrar na Movida e efetuar o pagamento do seu pedido.';
        break;
      default:
        break;
    }
  }

  // Quote Tariff

  getTariff() {
    if (Object.keys(this.pedido.protecao).length == 0 || !this.pedido.tarifa.Franquia || !this.pedido.tarifa.Periodo) {
      return;
    }

    this.carOpt = {
      protection: {
        sigla: this.pedido.protecao.Sigla,
      },
      values: {
        promo: null,
        total: null
      },
      extraProtections: [],
      additionalServices: this.pedido.servicos_adicionais,
      period: {
        meses: parseInt(this.pedido.tarifa.Periodo)
      },
      mileage: {
        valor: {
          sigla: this.pedido.tarifa.Sigla
        }
      },
      groupId: this.pedido.frota.Grupo,
      acriss: this.pedido.frota.Acriss
    }
    const bookingData = this.quotationS.getBookingPayload(this.carOpt);
    this.carOpt.bookingPayload = bookingData;
    //const quotation = this.quoteReservation(bookingData);
    // this.shared.setLoadingAtivo(false);
  }

  pushIfNotExists(obj, dataResp) {
    if (typeof dataResp.tipo !== 'undefined') {
      let notFound = true;
      Object.keys(obj).map((k) => {
        if (typeof obj[k].tipo !== 'undefined' && obj[k].tipo === dataResp.tipo) {
          notFound = false;
        }
      });
      if (notFound) { obj.push(dataResp); }
    }
  }

  async quoteReservation(bookingData) {
    if (this.isPayer == true) {
      const dataResp = {
        tipo: 'RESP_FINAN', // Não trocar esse string | A mudança deve ser feita no BE-0KM
        quantidade: 1
      }
      this.pushIfNotExists(bookingData.equipamentos, dataResp);
    }

    bookingData = {...bookingData , equipamentos_ocultos: true };
    this.checkoutS.quoteReservation(bookingData).subscribe(res => {
      try {
        if (res !== null && res !== undefined && res && res.success && res.data.success) {

          const VehAvail = res.data.VehAvailRSCore.VehVendorAvails.VehVendorAvail.VehAvails[0];

          const VehAvailCore = VehAvail.VehAvail.VehAvailCore || {};
          const AdditionalServices = VehAvail.VehAvail.AdditionalServices || false;
          const AdditionalServicesHidden = VehAvailCore.PricedEquipsHidden || [];

          if (typeof VehAvailCore.VehAvailInfo !== 'undefined' && typeof VehAvailCore.VehAvailInfo.PricedCoverages !== 'undefined') {
            this.getProtections(VehAvailCore.VehAvailInfo.PricedCoverages);
          }
          if (AdditionalServices !== false) {
            this.getAdditionalServices(AdditionalServices, AdditionalServicesHidden);
          }

          const periods = VehAvailCore.TotalCharge.Periods;
          const promotion = this.quotationS.getPromotion(VehAvailCore);
          this.month = periods.length;
          this.month_promo = promotion['promo_month'];
          this.isPromo = promotion['is_promo'] ? promotion['is_promo'] : false;

          let promo = this.isPromo ? periods[0].valor_mensal_total : null;
          let total = this.isPromo ? promotion['valor_mensal_total'] : periods[0].valor_mensal_total;

          const messagePromo = VehAvail;
          this.messagePromo = this.isPromo ? messagePromo.VehAvail.VehAvailCore.VehAvailInfo.MessagePromocaoVeiculo : '';
          this.messageSemPromo = (!this.isPromo && messagePromo.VehAvail.VehAvailCore.VehAvailInfo.MessageSemPromocao != '') ? messagePromo.VehAvail.VehAvailCore.VehAvailInfo.MessageSemPromocao : '';
          this.carOpt.values.promo = promo ? promo.toString() : null;

          this.carOpt.values.total = total.toString();

          this.totalValueMonth = this.formatNumber.getBRLCurrency(Number((total).toString()));

          this.carOpt.tariff = VehAvailCore.RentalRate.RateQualifier.RateQualifier;
          this.excessKm = this.formatNumber.getBRLCurrency(Number(VehAvailCore.RentalRate.RateDistance.Amount));

          this.promoValue = this.isPromo ? this.formatNumber.getBRLCurrency(Number(this.isPromo ? periods[0].valor_mensal_total : null)) : null;
          this.promoValue = this.isPromo ? this.promoValue.replace("R$", ""): null;

          this.totalValue = this.formatNumber.getBRLCurrency(Number(this.carOpt.values.total));
          this.totalValue = this.totalValue.replace("R$", "");


          // Criptografa o total para o prepagamento-pay
          if (this.isPromo) {
            this.checkoutS.encryptTotalPrepagamentoPay(Number(this.carOpt.values.promo));
          } else {
            this.checkoutS.encryptTotalPrepagamentoPay(Number(this.carOpt.values.total));
          }

          this.isLoadingPrice = false;
        } else {
          this.showErrorAlert();
        }
      } catch (error) {
        this.showErrorAlert();
      } finally {
        this.shared.setLoadingAtivo(false);
      }
    });
  }

  getProtections(pricedCoverages) {
    const protections = [];
    const extraProtections = [];

    const basicProtection = pricedCoverages.filter(x => x.PricedCoverage.Coverage.Code === 'MB0KM')[0];
    const basicValue = basicProtection ? parseFloat(basicProtection.PricedCoverage.Charge.Calculation.UnitCharge) * 30 : 0;

    pricedCoverages.map(coverage => {
      const code = coverage.PricedCoverage.Coverage.Code;
      const value = coverage.PricedCoverage.Charge.Calculation.UnitCharge;

      if (code === 'MC0KM' || code === 'MB0KM') {
        const protection = {
          id: this.quotationS.getProductId(code),
          coparticipacao: coverage.PricedCoverage.Coverage.Coparticipacao,
          price_id: this.quotationS.getProductPriceId(code),
          sigla: code,
          nome: coverage.PricedCoverage.Coverage.Details[0].value,
          descricao: coverage.PricedCoverage.Coverage.Details[1].value,
          valor: code === 'MC0KM' ? ((parseFloat(value) * 30) - basicValue).toFixed(2) : (0).toFixed(2),
          copart: this.quotationS.getCoinsurance(this.carOpt.groupId, code),
          extra: false,
          selected: coverage.PricedCoverage.Charge.IncludedInRate,
          panelDetail: 'out',
          coverageDescriptions: code === 'MC0KM' ? [
            'Cobertura para danos corporais causados a terceiros limitada a R$250M;',
            'Cobertura para danos materiais causados a terceiros limitada a R$250M;',
            'Cobertura para danos morais causados a terceiros limitada a R$30M.'
          ] : null,
          conductorsDescription: 'A cobertura engloba também outros condutores, que possam dirigir o veículo, contanto que sejam maiores de 21 anos e tenham habilitação há mais de 2 anos.'
        }
        if (coverage.PricedCoverage.Charge.IncludedInRate) {
          this.carOpt.protection = protection;
        }
        protections.push(protection);
      }
    });

    if (protections.length === 0) {
      this.showErrorAlert();
    } else {
      this.protectionList = protections.sort((a, b) => a.sigla < b.sigla ? -1 : 1);
      this.extraProtectionList = extraProtections.sort((a, b) => a.sigla < b.sigla ? -1 : 1);
    }
  }

  getAdditionalServices(pricedEquips, pricedEquipsHidden:any[] = []) {
    const additionalServices = new Array<any>();
    additionalServices.push({
      id: this.quotationS.getProductId('ADD'),
      id_vetor: this.quotationS.getProductVetorId('ADD'),
      price_id: this.quotationS.getProductPriceId('ADD'),
      sigla: 'ADD',
      img_url: `assets/img/icones-seguranca-servicos/ADD.png`,
      sku: '1',
      nome: 'Condutor adicional',
      descricao: 'Inclua até 3 condutores adicionais para dirigir o seu possante!',
      valor: '0',
      checked: false,
      panelDetail: 'out',
    });

    pricedEquips.map(pricedEquip => {
      const equipment = pricedEquip.PricedEquip.Equipment;
      const charge = pricedEquip.PricedEquip.Charge;
      const sigla = equipment.Sigla;

      const indexpricedEquipsHidden = pricedEquipsHidden
        .findIndex(pricedEquipsHiddenItem => pricedEquipsHiddenItem.PricedEquip.Equipment.Sigla == sigla)

      if (indexpricedEquipsHidden > -1) {
        return;
      }

      let img_url = `assets/img/icones-seguranca-servicos/${sigla.toUpperCase()}.svg`;

      if (sigla != 'SPZKM') {
        img_url = `assets/img/icones-seguranca-servicos/${sigla.toUpperCase()}.png`;
      }

      if (pricedEquip.PricedEquip.Equipment.PictureURL3.indexOf('png')) {
        img_url = pricedEquip.PricedEquip.Equipment.PictureURL3;
      }

      const equip = {
        id: this.quotationS.getProductId(sigla),
        id_vetor: this.quotationS.getProductVetorId(sigla),
        price_id: this.quotationS.getProductPriceId(sigla),
        sigla: sigla,
        sku: '1',
        img_url,
        nome: equipment.Description,
        descricao: equipment.Details,
        valor: sigla === 'CZKM' ? '0' : (parseFloat(charge.Calculation.UnitCharge) * 30).toFixed(2),
        checked: charge.IncludedInRate === 'true' ? true : false,
        panelDetail: 'out',
      }
      additionalServices.push(equip);
    });

    this.additionalServiceList = additionalServices.sort((a, b) => a.sigla < b.sigla ? -1 : 1);
    this.shared.setCounterLoading(false);
  }

  showErrorAlert(msg = null) {
    if (this.triggerAlert) {
      this.shared.setLoadingAtivo(false);
      this.triggerAlert = false;
      const message = msg ? msg : 'Não foi possível carregar as informações do veículo. Por favor, tente novamente mais tarde.';
      this.alertMsg.openDialog({
        tipo: 'redirect',
        titulo: 'Desculpe,',
        mensagem: message
      }, 'redirect', true);
    }
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
    this.timerClosed = true;
  }


  nextStep(status){
    switch (status) {
      case 'resume':
        this.setPageStatus('method_payment')
        break;
      case 'method_payment':
        if(this.ownCard) this.setPageStatus('own_card')
        if(this.holderCard) this.setPageStatus('resp_fin')
        break;

      default:
        break;
    }

  }

  getDeliveryTime() {

    if (this.pedido) {
      const date1 = new Date(this.pedido.DataPedido);
      const date2 = new Date(this.pedido.DataPrevistaRetirada);

     // To calculate the time difference of two dates
      const Difference_In_Time = date2.getTime() - date1.getTime();

      // To calculate the no. of days between two dates
      return Math.round(Difference_In_Time / (1000 * 3600 * 24)) + 1;
    }
  }

  getTotal() {

    const sanitize = (value: string) => {
      return value.replace('R$', '');
    };

    if(this.isPromo && this.promoValue) {

      return sanitize(this.promoValue);

    } else if(!this.isPromo && this.totalValue) {

      return sanitize(this.totalValue);
    }

    return sanitize(' ... ');
  }
}
