import { Injectable } from '@angular/core';
import { FormControl } from '@angular/forms';

@Injectable({
    providedIn: 'root'
})
export class CPFValidatorService {

    constructor() { }

    public isValidCpf(formControl: FormControl): any {

        if (!formControl.value) {
            return null;
        }

        const INVALID_CPF_NUMBERS: Array<string> = ['00000000000', '11111111111', '22222222222', '33333333333', '44444444444', '55555555555', '66666666666', '77777777777', '88888888888', '99999999999', '12345678909'];

        const REGEX_CPF_ONLY_WITH_NUMBERS: RegExp = /[.-]/g;
        const REGEX_CPF_NOT_ONLY_NUMBERS: RegExp = /[^\d]/g;

        const removeInvalidCharactersFn = (cpf: string, onlyNumbers?: boolean): string => {
            const regex: RegExp = onlyNumbers ? REGEX_CPF_ONLY_WITH_NUMBERS : REGEX_CPF_NOT_ONLY_NUMBERS;
            return (cpf || '').replace(regex, '');
        }

        const CPF: string = removeInvalidCharactersFn(formControl.value, true);

        if (!CPF || CPF.length !== 11 || INVALID_CPF_NUMBERS.includes(CPF)) {
            return {
                cpfNotValid: true
            };
        }

        const checkDigitsFn = (digits: string): number => {

            const numbers: Array<number> = digits.split('').map(number => {
                return parseInt(number, 10);
            });

            const modulus: number = numbers.length + 1;

            const multiplied: Array<number> = numbers.map((number, index) => {
                return number * (modulus - index);
            });

            const modulusOfEleven: number = multiplied.reduce((buffer, number) => {
                return (buffer + number);
            }) % 11;

            const modulusOfElevenLessThanTwo: boolean = modulusOfEleven < 2;
            const elevenMinusModulusOfEleven: number = (11 - modulusOfEleven);

            return modulusOfElevenLessThanTwo ? 0 : elevenMinusModulusOfEleven;
        }

        let numbers: string = CPF.substr(0, 9);

        numbers += checkDigitsFn(numbers);
        numbers += checkDigitsFn(numbers);

        const cpfNotValid: boolean = numbers.substr(-2) !== CPF.substr(-2);

        return (!cpfNotValid) ? null : {
            cpfNotValid: cpfNotValid
        };
    }
}