import { Component, OnInit } from '@angular/core';

// Services
// import { CartaoCreditoService } from '../../services/cartao-credito/cartao-credito.service';
import { CartoesConsultaService } from 'src/app/core/services/cartoes.service';

// Models
import { CartaoCreditoNovoModel } from 'src/app/shared/models/cartao-credito-novo.model';

//Valida Cartao
import * as cardValidator from 'card-validator';
import { CheckoutService } from 'src/app/core/services/checkout.service';
import { SwiperConfigInterface } from 'ngx-swiper-wrapper';
import { SubSink } from 'subsink';
import { EmitEvent, EventBusService, Events } from 'src/app/core/services/eventbus.service';
import { MainSharedService } from 'src/app/util/shared.service';

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'movida-cartao-credito-mobile',
  templateUrl: './cartao-credito-mobile.component.html',
  styleUrls: ['./cartao-credito-mobile.component.scss']
})
export class CartaoCreditoMobileComponent implements OnInit {

  constructor(
    public cartoesConsultaService: CartoesConsultaService,
    public checkoutS: CheckoutService,
    private eventBusService: EventBusService,
    private sharedService: MainSharedService
  ) {

    this.setSwiperConfig();
    
    if (this.sharedService.user && this.sharedService.user.razaosocial) {
      
      let titularUser = this.sharedService.user.razaosocial
      let firstName = titularUser.split(' ',3)[0]
      let middleName = titularUser.split(' ',3)[2] !== undefined ? (titularUser.split(' ',3)[1].length > 2 ? titularUser.split(' ',3)[1].substring(0,1) : titularUser.split(' ',3)[1]) : titularUser.split(' ',3)[1]
      let lastName = titularUser.split(' ',3)[2] !== undefined ? titularUser.split(' ',3)[2] : ""
      this.titular = firstName + " " + middleName + " " + lastName;

      this.cartoesConsultaService.getCartoes().subscribe(cards => {
        this.eventBusService.emit(new EmitEvent(Events.checkCards, cards.data));
        
        let cartoes = cards.data;

        this.exibirSwipper = true;
  
        
        if (!Object.keys(cartoes).length) {
          return;
        }
        
        this.checkedRadio = cartoes[0].CartaoID;
        this.eventBusService.emit(new EmitEvent(Events.sendDataCard, cartoes[0]));
  
        let cartoes$ = [];
  
        for (let cartao in cartoes) {
          let numFormat = '';
          let finalCartao = '';
  
          for (let cont in cartoes[cartao]['Nro']) {
            let numCartao = cartoes[cartao]['Nro'];
  
            if (numCartao.length < 13) {
              numFormat = ('' + cartoes[cartao]['Nro']).slice(-6, -2) + ' ' + 'XXXX XXXX XXXX';
            } else {
              numFormat = 'XXXX XXXX XXXX ' + numCartao.substr(12);
              finalCartao = numCartao.substr(12);
            }
  
            // Visa - Inicia com 4
            if (Number(numCartao.substr(0, 1)) === 4) {
              numFormat = 'XXXX XXXX XXXX ' + numCartao.substr(12);
              finalCartao = numCartao.substr(12);
            }
            // MasterCard  - Inicia com 5 e sequencia de 1-9
            if (Number(numCartao.substr(0, 2)) === 5[1 - 9]) {
              numFormat = 'XXXX XXXX XXXX ' + numCartao.substr(12);
              finalCartao = numCartao.substr(12);
            }
            //Dinner 301,305,36,38
            if (Number(numCartao.substr(0, 2)) === 30 ||
              Number(numCartao.substr(0, 2)) === 36 ||
              Number(numCartao.substr(0, 2)) === 38) {
              numFormat = 'XXXX XXXXXX XX' + numCartao.substr(12);
              finalCartao = numCartao.substr(12);
            }
            // Discover 6011,622,64,65
            if (Number(numCartao.substr(0, 2)) === 64 ||
              Number(numCartao.substr(0, 2)) === 65 ||
              Number(numCartao.substr(0, 4)) === 6011 ||
              Number(numCartao.substr(0, 3)) === 622) {
              numFormat = 'XXXX XXXX XXXX ' + numCartao.substr(12);
              finalCartao = numCartao.substr(12);
            }
            // JCB - Inicia com 35
            if (Number(numCartao.substr(0, 2)) === 35) {
              numFormat = 'XXXX XXXX XXXX ' + numCartao.substr(12);
              finalCartao = numCartao.substr(12);
            }
            // Aura - Inicia com 50
            if (Number(numCartao.substr(0, 2)) === 50) {
              numFormat = 'XXXX XXXX XXXX ' + numCartao.substr(12);
              finalCartao = numCartao.substr(12);
            }
            // HiperCard - Inicia com 60
            if (Number(numCartao.substr(0, 2)) === 60) {
              numFormat = 'XXXX XXXX XXXX ' + numCartao.substr(12);
              finalCartao = numCartao.substr(12);
            }
            // Amex
            if (Number(numCartao.substr(0, 2)) === 34 ||
              Number(numCartao.substr(0, 2)) === 37) {
              numFormat = 'XXXX XXXXXX X' + numCartao.substr(12);
              finalCartao = numCartao.substr(12);
            }
          }
  
          if (cartoes[cartao]['CartaoID']) {
  
            const data = {
              CartaoID: cartoes[cartao]['CartaoID'],
              Nro: numFormat,
              Validade: cartoes[cartao]['Validade'],
              Titular: cartoes[cartao]['Titular'],
              BandeiraID: cartoes[cartao]['BandeiraID'],
              FinalCartao: finalCartao
            };
  
            cartoes$.push((data));
  
          }
  
          this.cartoes$ = cartoes$;
  
        }
      });
      
    }
  }

  // Mascara para Nro Cartao
  public type: any | 'any';
  public cardnumber: any;
  public cardnum = '';
  public mask = {};
  public cartaoValido: boolean;
  public digitosCartao = 16;
  public dataInvalida: boolean = false
  public cartoes$ = [];
  public show = true;
  public disabled = false;
  public config: SwiperConfigInterface;
//  private indexCartao = 0;
  public cardClicked: string = "";
  public checkedRadio: string = "";
  public titular: any;
  public selectNewCard: boolean = false;
  public exibirSwipper: boolean = false;
  public cardNumberFieldBlocked: boolean = false;
  public indexSlide: number;

  private sub = new SubSink();

  public cartaoModel: CartaoCreditoNovoModel = {
    Titular: '',
    CarteiraID: 1,
    cpf: '',
    DeviceFingerprintID: '123456789123456789',
    Ip: '192.168.0.25',
    Tipo: 'C',
    Nro: '',
    Validade: '',
    BandeiraID: '',
    Bandeira: '',
    cvv: ''
  };

  public cardParams: any = {
    debounces: {
      bandeira: false
    }
  };

  public setCVV(cvv) {

    this.cartaoModel.cvv = cvv
    this.sendDataNewCard();
  }

  public processCardDataChange(value, field = '') {

    if (this.cardNumberFieldBlocked) {
      return false;
    }

    this.cartaoModel.cpf = this.sharedService.user && this.sharedService.user.cpf ? this.sharedService.user.cpf : '';

    let mask16 = {
      mask: [/[0-9]/, /[0-9]/, /[0-9]/, /[0-9]/, '-',
        /[0-9]/, /[0-9]/, /[0-9]/, /[0-9]/, '-',
        /[0-9]/, /[0-9]/, /[0-9]/, /[0-9]/, '-',
        /[0-9]/, /[0-9]/, /[0-9]/, /[0-9]/]
    };

    // Visa
    if (Number(this.cartaoModel.Nro.substr(0, 1)) === 4) {
      this.mask = mask16;
    }
    // MasterCard
    if (Number(this.cartaoModel.Nro.substr(0, 2)) === 51 ||
      Number(this.cartaoModel.Nro.substr(0, 2)) === 52 ||
      Number(this.cartaoModel.Nro.substr(0, 2)) === 53 ||
      Number(this.cartaoModel.Nro.substr(0, 2)) === 54 ||
      Number(this.cartaoModel.Nro.substr(0, 2)) === 55) {
      this.mask = mask16;
    }
    //Dinner
    if (Number(this.cartaoModel.Nro.substr(0, 2)) === 30 ||
      Number(this.cartaoModel.Nro.substr(0, 2)) === 36 ||
      Number(this.cartaoModel.Nro.substr(0, 2)) === 38) {
      this.mask = {
        mask: [/[0-9]/, /[0-9]/, /[0-9]/, /[0-9]/, '-',
          /[0-9]/, /[0-9]/, /[0-9]/, /[0-9]/, /[0-9]/, /[0-9]/, '-',
          /[0-9]/, /[0-9]/, /[0-9]/, /[0-9]/]
      };

      this.digitosCartao = 14;
    }
    // Discover
    if (Number(this.cartaoModel.Nro.substr(0, 2)) === 65 ||
      Number(this.cartaoModel.Nro.substr(0, 4)) === 6011) {
      this.mask = mask16;
    }
    // JCB
    if (Number(this.cartaoModel.Nro.substr(0, 2)) === 35) {
      this.mask = mask16;
    }
    // Amex
    if (Number(this.cartaoModel.Nro.substr(0, 2)) === 34 ||
      Number(this.cartaoModel.Nro.substr(0, 2)) === 37) {
      this.mask = {
        mask: [/[0-9]/, /[0-9]/, /[0-9]/, /[0-9]/, '-',
          /[0-9]/, /[0-9]/, /[0-9]/, /[0-9]/, /[0-9]/, /[0-9]/, '-',
          /[0-9]/, /[0-9]/, /[0-9]/, /[0-9]/, /[0-9]/]
      };

      this.digitosCartao = 15;
    }

    let numCartao = this.cartaoModel.Nro.replace(/\D/g, '');

    if (field === 'Nro' && value !== this.cartaoModel.Nro) {

      this.cartaoModel.Nro = numCartao;

      if (this.cartaoModel.Nro.length === this.digitosCartao) {

        this.checkoutS.getCardBrand(this.cartaoModel.Nro).subscribe((res: any) => {
          let card = res.data
          if (res.data.success) {
            this.cartaoModel.BandeiraID = card.bandeiraID;
            this.cartaoModel.Bandeira = card.bandeira;
            this.cartaoValido = true;
          } else {
            this.cartaoModel.BandeiraID = '';
            this.cartaoModel.Bandeira = '';
            this.cartaoModel.cvv = '';
            this.cartaoModel.Validade = '';
            this.cartaoValido = false;
            this.digitosCartao = 16;
          }
          
          this.formatarDadosCartao();

        }, e => {
        });

      } else {
        this.cartaoValido = false;
        this.digitosCartao = 16;
        this.cartaoModel.Bandeira = '';
        this.cartaoModel.BandeiraID = '';
      }

    } else if (field === 'Validade' && value !== this.cartaoModel.Validade && this.cartaoModel.Validade.length >= 4) {

      let ano = this.cartaoModel.Validade.substr(2, 6);
      let mes = this.cartaoModel.Validade.substr(0, 2);
      let anoAtual = new Date().getFullYear();

      // Valida Ano
      if (ano.length === 2) {
        ano = '20' + ano;
      }

      // valida formato
      if (Number(mes) > 12 || (ano.length !== 4 || Number(ano) < anoAtual)) {
        // this.cartaoService.alert.error('Erro!', 'Formato da data inválido, o formato aceitável é: mm/aaaa');
        this.dataInvalida = true;
        this.cartaoModel.Validade = '';
      } else {
        this.dataInvalida = false;
        this.cartaoModel.Validade = mes + '/' + ano;
      }

      const dataObj = new Date();
      const dataMes = ('20' + (dataObj.getMonth() + 1)).slice(-2);
      const dataAno = dataObj.getFullYear();

      if (Number(ano) < dataAno || Number(ano) === dataAno && Number(mes) < Number(dataMes)) {
        this.cartaoModel.Validade = '';
        // this.cartaoService.alert.error('Cartão Vencido!', 'Por favor, insira um vencimento válido.');
        return;
      }
    } else {
      this.cartoesConsultaService.cartaoDados.next(this.cartaoModel);
    }
    this.formatarDadosCartao();
  }

  public validateForm() {

    if (this.cartaoModel.Titular.split(' ').length >= 2) {
      if (this.cartaoValido === true) {
        if (this.cartaoModel.Bandeira.length > 0) {
          if (typeof this.cartaoModel.Validade === 'string' && this.cartaoModel.Validade.replace(/\D/g, '').length === 6) {
            if (this.cartaoModel.cvv.toString().length >= 3 && this.cartaoModel.cvv.toString().length <= 4) {
              return true;
            }
          }
        }
      }
    }

    return false;
  }

  public sendDataCard(card) {
    this.eventBusService.emit(new EmitEvent(Events.sendDataCard, card));
  }

  public sendDataNewCard() {
    let data = {} as CartaoCreditoNovoModel;
    Object.assign(data, this.formatarDadosCartao());
    this.eventBusService.emit(new EmitEvent(Events.sendNewCard, data));
  }

  public formatarDadosCartao() {
    let dados = {} as CartaoCreditoNovoModel;
    Object.assign(dados, this.cartaoModel);
    // dados.Validade = '01/' + dados.Validade.substring(0, 2) + '/20' + dados.Validade.substring(2, 4);
    dados.Validade = '01/' + dados.Validade.substr(0, 2) + '/20' + dados.Validade.substr(4, 6);
    if (this.cartaoModel.Titular !== "") {
      dados.Titular = this.cartaoModel.Titular
    }
    this.eventBusService.emit(new EmitEvent(Events.sendNewCard, dados));
    return dados;
  }

  ngOnInit(): void {

    this.sub.sink = this.eventBusService.on(Events.sendCVV, cvv => this.setCVV(cvv));

    this.mask = {
      mask: [/[0-9]/, /[0-9]/, /[0-9]/, /[0-9]/, '-',
        /[0-9]/, /[0-9]/, /[0-9]/, /[0-9]/, '-',
        /[0-9]/, /[0-9]/, /[0-9]/, /[0-9]/, '-',
        /[0-9]/, /[0-9]/, /[0-9]/, /[0-9]/]
    };

  }

  onPasteCardNumber(event: ClipboardEvent) {
    this.cartaoModel.Nro = event.clipboardData.getData('text').replace(/\D/g, '');
    this.processCardDataChange('', 'Nro');
    this.cardNumberFieldBlocked = true;

    // setTimeout para evitar gerar 3 ou mais requests duplicadas (paste, keyup e change)
    setTimeout(() => {
      this.cardNumberFieldBlocked = false;
    }, 3000);
  }

  updateCard(): void {
    this.cardnumber = this.cardnum.split(/[\-\s]+/ig).join('');
    this.type = cardValidator.number(this.cardnumber);
  }

  somente_letras(event) {
    var k;
    k = event.charCode;  //
    return ((k > 64 && k < 91) || (k > 96 && k < 123) || k === 8 || k === 32);
  }

  public onResize($event) {
    this.setSwiperConfig();
  }

  public checkRadio(idCard) {
    this.checkedRadio = idCard;

    if(idCard == "new_card"){
      this.selectNewCard = true;
      this.eventBusService.emit(new EmitEvent(Events.changeCardType, 'newCard'));
    }else{
      this.eventBusService.emit(new EmitEvent(Events.changeCardType, 'ownCard'));
    }
  }

  public isDesktop() {
    return window.outerWidth > 1188;
  }


  public setSwiperConfig() {
    this.config = {
      direction: 'horizontal',
      loop: this.isDesktop() ? this.cartoes$.length > 1 : false,
      observer: false,
      initialSlide: 0,
      slidesPerView: !this.isDesktop() ? 1 : 2,
      keyboard: true,
      mousewheel: true,
      scrollbar: false,
      navigation: true, 
      pagination: !this.isDesktop(),
    };
  }

  public slideChange(event){
    let count_cartao = this.cartoes$.length;
    if(count_cartao == event){
      this.checkRadio('new_card'); 
      this.sendDataNewCard();
    }else{
      let cartao = this.cartoes$[event]
      this.checkRadio(cartao.CartaoID); 
      this.sendDataCard(cartao);
    }
  }

}
